td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
html {
  box-sizing: border-box;
  font-size: 16px;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
.Grid {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  padding: 0;
  margin: 0;
}
.Grid--fixed {
  max-width: 100%;
  width: 1200px;
  margin: 0 auto;
}
.Grid--flex_cols .Grid__col {
  display: flex;
}
.Grid--gutters {
  margin: -1rem 0 1rem -1rem;
}
.Grid--gutters .Grid--gutters {
  margin: -1rem 0 1rem 0;
}
.Grid--gutters.Grid--fixed {
  margin: -1rem auto 1rem auto;
}
.Grid--gutters .Grid__col {
  padding: 1rem 0 0;
}
.Grid--gutters .Grid__col:first-child {
  padding-left: 0;
}
.Grid__col {
  flex: 0 0 100%;
}
.Grid__col--xs-1 {
  width: 8.333333333333334%;
  flex: none;
}
.Grid__col--xs-2 {
  width: 16.666666666666668%;
  flex: none;
}
.Grid__col--xs-3 {
  width: 25%;
  flex: none;
}
.Grid__col--xs-4 {
  width: 33.333333333333336%;
  flex: none;
}
.Grid__col--xs-5 {
  width: 41.66666666666667%;
  flex: none;
}
.Grid__col--xs-6 {
  width: 50%;
  flex: none;
}
.Grid__col--xs-7 {
  width: 58.333333333333336%;
  flex: none;
}
.Grid__col--xs-8 {
  width: 66.66666666666667%;
  flex: none;
}
.Grid__col--xs-9 {
  width: 75%;
  flex: none;
}
.Grid__col--xs-10 {
  width: 83.33333333333334%;
  flex: none;
}
.Grid__col--xs-11 {
  width: 91.66666666666667%;
  flex: none;
}
.Grid__col--xs-12 {
  width: 100%;
  flex: none;
}
