h1,
h2,
h3,
h4,
h5,
h6,
span,
div,
p,
small {
  font-family: 'Roboto', sans-serif;
}
.Section__header {
  padding-top: 2rem;
}
.Section__title {
  text-align: center;
  margin-bottom: 1rem;
  font-size: 2rem;
  font-weight: 600;
}
.Section__subtitle {
  font-weight: 100;
  font-size: 1.5rem;
  display: block;
  text-align: center;
}
.Section__underline {
  width: 200px;
  height: 2px;
  display: block;
  margin: 1.5rem auto;
}
.Section__body {
  padding: 2rem 0;
}
.Section--dark {
  background-color: #1a1a14;
}
.Section--dark .Section__title {
  color: #e6e6e6;
}
.Section--dark .Section__underline {
  background-color: #00aaed;
}
.Section--dark .Section__subtitle {
  color: #e6e6e6;
}
.Section--light {
  background-color: #e6e6e6;
}
.Section--light .Section__title {
  color: #1a1a14;
}
.Section--light .Section__underline {
  background-color: #3bc7ff;
}
.Section--light .Section__subtitle {
  color: #1a1a14;
}
.Block {
  overflow: hidden;
  padding: 1rem;
  width: 100%;
}
.Block__content {
  word-wrap: break-word;
  overflow: hidden;
  line-height: 1.5rem;
  font-weight: 300;
}
.Block:after {
  display: block;
  content: "";
  height: 0;
  visibility: hidden;
}
.Section--dark .Block {
  background-color: rgba(255,255,255,0.2);
}
.Section--dark .Block__content {
  color: #e6e6e6;
}
.Section--light .Block {
  background-color: rgba(0,0,0,0.2);
}
.Section--light .Block__content {
  color: #1a1a14;
}
